import React from 'react';
import {login} from './actions'
import store from './store'

const Login = (props) => {
    let log = () => {
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login';
        fetch(url, {
            method: 'post',
            headers: {
              'Content-Type': 'application/json'
            },
        })
        .then(res=>res.json())
        .then((res) => {store.dispatch(login(res.data.name));console.log(store.getState())})
        
    }
    return <div>
        <button onClick={()=>{
            log();
            
            console.log(store.getState());
            props.location.state.val === '/star' ? props.history.replace(props.location.state.val) : props.history.goBack()
            }}>登录</button>
    </div>
}
export default Login;