import React from 'react';
import { connect } from 'react-redux';
import {takestar} from './actions'
import './itemmsg.css'
import store from './store';

function getcolor(){
    return {isstar: false}
}
function changestar(){
    this.setState({isstar: !this.state.isstar})
}
const Itemmsg = (props) => {

        let item = props.location.state.item

        // console.log(item)
        // let id = Number(props.match.url.split(':')[1]);
        // let num = 0;
        // props.itemList.map((item, idx) => {
        //     if(item.id === id)num = idx;
        // })
        // let item = props.itemList[num]
// console.log(props.itemList[num])
        return <div className='body'>
            <i className="iconfont icon-fanhui"  onClick={()=>props.history.goBack()}></i>
            <img src={item.img} className='img'></img>
            <p>{item.price}</p>
            <p>{item.title}</p>
            
            <div className='bott'>
                <div className='but'>
                    <i className="iconfont icon-dianpu but_1"></i>
                    <p>店铺</p>
                </div>
                <div className='but'>
                    <i className="iconfont icon-kefu but_2"></i>
                    <p>客服</p>
                </div>
                <div  onClick={()=>{
                    //点击收藏后登录，登录完成之后自动收藏
                    if(store.getState().items.username === ''){
                        props.history.push({pathname:'/login',state:{val:'/itemmsg/:' + item.id}})
                    }
                    props.dispatch(takestar(item.id));
                    console.log('add')
                    }}
                    className={`but ${props.starList.indexOf(item.id) == -1?null:"active"}`} >
                         {/* */}
                    <i className="iconfont icon-shoucang but_3"></i>
                    <p>收藏</p>
                </div>

                <div className='tbut'>
                    <button className='but_4'>加入购物车</button>
                    <button className='but_5'>立即购买</button>
                </div>
            </div>
            
        </div>
    // }
}


const getitemlist = (state) => {
    // console.log(state)
    return {
        starList: state.items.starList
    }
}

export default connect(getitemlist)(Itemmsg);