import React from 'react';
import reducer from './reducer';
import store from './store';
import {login, logout, showitem, showstar, takestar,test} from './actions';
import {connect} from 'react-redux';
import TheCarousel from './Carousel';
import './tbhome.css';
import Items from './Items'



const getlist = () => {
        let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist';
        fetch(url,{
            method: 'post',
            headers: {
                'Content-Type': 'application/json'
            },
        }).then((res) => res.json())
        .then((res) => {store.dispatch(showitem(res.data))})
        // .then(res=>{console.log(store.getState())})
        .catch((e) => { console.log(e.message) })
    }
const ggg = async () => {
    await getlist()
}

const Tbhome = (props) => {

    store.dispatch(test(2))
    // console.log(store.getState())
    
    ggg()
    // getlist()


    return <div>
        <header>
            <i className="iconfont icon-taobao2" ></i>
            <input type="text" defaultValue= {"寻找宝贝店铺"}onClick={()=>props.history.push('/search')}/>
        </header>
        <TheCarousel/>
        <div>
            <Items/>
        </div>
    </div>
}

// export {getlist}
// export default Tbhome
export default connect()(Tbhome);