import React from 'react';
import store from './store'
import { connect } from 'react-redux'
import { takestar } from './actions'
// import {getlist} from './tbhome'

// class Items extends React.Component {

//     render(props){
        
        // console.log(store.getState())
const Star = (props) => {        
        // if(store.getState().items.username === ''){
        //     console.log("需要跳转");
        //     props.history.push('/login')
        //     return;
        // }
        // let itemList = store.getState().itemList;
        // console.log(store.getState())
        // console.log(props.starList)
        return <div className="line">
            <ul>
                {
                    props.itemList.map((item, i) => 
                        props.starList.indexOf(item.id) != -1 
                        ? <li key={i} className="item" style={{marginLeft:"12px", marginBottom:"15px"}}>
                            <img src={item.img} alt=""/>
                            <p>{item.title}</p>
                            <div>
                                <p>{item.price}</p>
                                <p onClick={()=>{props.dispatch(takestar(item.id));console.log('del')}}>取消收藏</p>
                            </div>
                        </li>
                        :null

                    )
                }
            </ul>  
        </div>
    }


const getstarlist = (state) => {
    // console.log(state)
    return {
        itemList: state.items.itemList,
        starList: state.items.starList
    }
}
    
export default connect(getstarlist)(Star);