import { Carousel, WingBlank } from 'antd-mobile';
import React from 'react';

// window.addEventListener('touchmove', func, { passive: false })

class TheCarousel extends React.Component {
  state = {
    data: ['1', '2', '3', '4', '5'],
    imgHeight: 130,
  }
  componentDidMount() {
    // simulate img loading
    setTimeout(() => {
      this.setState({
        data: ['https://gw.alicdn.com/imgextra/i2/69/O1CN012kgqDf1CNdxWjfTPO_!!69-0-lubanu.jpg', 'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg', 'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000006904/O1CN01dLPgGK20s54ErU225_!!6000000006904-0-octopus.jpg', 'https://gw.alicdn.com/imgextra/i3/191/O1CN01VWfpT01DHWHQurTZ7_!!191-0-lubanu.jpg', 'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i3/6000000005710/O1CN013xG97J1s3EAHqQry6_!!6000000005710-0-octopus.jpg'],
      });
    }, 100);
  }
  render() {
    return (
      <WingBlank style={{width:'375px', marginLeft:'0px',touchAction: 'none', paddingTop:'45px'}}>
        <Carousel
          autoplay={true}
          infinite={true}
          slideWidth="375px"
          autoplayInterval={3000}
          slideWidth={1}
          // beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          // afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="https://main.m.taobao.com/?sprefer=sypc00"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                src={val}
                alt=""
                style={{ width: '100%', verticalAlign: 'top' }}
                
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: '120px' });
                }}
              />
            </a>
          ))}
        </Carousel>
      </WingBlank>
    );
  }
}

export default TheCarousel;