import React from 'react';
import { NavLink } from 'react-router-dom';
import './items.css'
import store from './store'
import {connect} from 'react-redux'
// import {getlist} from './tbhome'

// class Items extends React.Component {

//     render(props){
        
        // console.log(store.getState())
const Items = (props) => {        

        // let itemList = store.getState().itemList;
        // console.log(store.getState())
        // console.log(props)
        return <div className="line">
            <ul>
                {
                    props.itemList.map((item, i) => (
                        <NavLink key={i} to={{pathname:'/itemmsg/:' + item.id, state: {item:item}}} className='nav'>
                            <li className="item">
                                <img src={item.img} alt=""/>
                                <p>{item.title}</p>
                                <div>
                                    <p>{item.price}</p>
                                </div>
                            </li>
                        </NavLink>
                    ))
                }
            </ul>  
        </div>
    }


const getitemlist = (state) => {
    // console.log(state)
    return {
        itemList: state.items.itemList
    }
}
    
export default connect(getitemlist)(Items);