import React from 'react';

import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';

class Search extends React.Component {

  render() {
    return (<div>
      <SearchBar placeholder="Search" defaultValue='宝贝' showCancelButton={true} maxLength={8} onCancel={() =>this.props.history.goBack()} />
      <WhiteSpace />
    </div>);
  }
}




export default Search;