import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import store from './store'

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: '',
        };
    }
    render() {
        const { pathname } = this.props.location;
        console.log(pathname)
        if(pathname.split('/').length > 2 || pathname.split('/')[1] ==='login'){
            return null; 
        }
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#777"
                    tabBarPosition='bottom'
                    tintColor="#777"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={<i className='iconfont icon-home1'></i>}
                        selectedIcon={
                            <i className='iconfont icon-home' ></i>
                        }   
                        title="首页"
                        key="tbhome"
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-dilanxianxingiconyihuifu_huabanfuben'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-ai66'></i>                           
                        }
                        title="收藏"
                        key="star"
                        selected={ pathname === '/star' }
                        onPress={ () => {
                            // console.log(store.getState().items.username)
                            if(store.getState().items.username === ''){
                                this.props.history.push({pathname:'/login', state:{val: '/star'}})
                                // this.props.history.replace('/star')
                            }else{
                                this.props.history.push('/star')
                            }
                            
                        } }
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-weibiao45127'></i>}
                        selectedIcon={
                            <i className='iconfont icon-list2'></i>
                        }
                        title="订单列表"
                        key="list"
                        selected={ pathname === '/list' }
                        onPress={ () => {
                            this.props.history.push('/list')
                        } }
                    >
                        
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-wode'></i> }
                        selectedIcon={<i className="iconfont icon-wode1"></i>}
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === '/mytaobao' }
                        onPress={ () => {
                            this.props.history.push('/mytaobao')
                        } }
                    >
                        
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);