import { combineReducers } from 'redux';


const Msg = {
    itemList: [],
    starList: [],
    username: '',
    num: 0
}
function items(state = Msg, action) {
    switch(action.type){
        case 'LOGIN':
            return {
                ...state,
                username: action.value
            }
        case "aaa":
            return {
                ...state,
                num: action.value
            }
        case "showItem":
            return {
                ...state,
                itemList: action.value
            }
        case "showStar":
            return {
                ...state,
                starList: action.value
            }
        case "takeStar":
            let arr = [...state.starList];
            let check = arr.indexOf(action.value)
            if(check == -1){
                arr.push(action.value)
            }else{
                arr.splice(check, 1)
            }
            return {
                ...state,
                starList: arr
            }
        default:
            return state
  }
}

const reducer = combineReducers({
  items
})

export default reducer;