import React,{useState,useEffect} from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom';
import store from './store';
import Mytab from './mytab';
import Tbhome from './tbhome';
import Search from './Search';
import Itemmsg from './Itemmsg';
import Star from './Star';
import Login from './Login';
import List from './List';
import Mytaobao from './Mytaobao';
import { Provider } from 'react-redux';


const TaobaoApp = () => {
    return <Provider store={store}>
        <Router>
        <Switch>
            <Route exact path="/" component={Tbhome}/>
            <Route path='/star' component={Star}/>
            <Route path='/list' component={List}/>
            <Route path='/mytaobao' component={Mytaobao}/>
            <Route path='/login' component={Login}/>
            <Route path='/search' component={Search}/>
            <Route path='/itemmsg/:id' component={Itemmsg}/>
            <Route render={()=><Redirect to='/'/>}/>
        </Switch>
        <Mytab />
    </Router>
</Provider>
}

export default TaobaoApp;